.services__container{
    display: flex;
    justify-content: center;
    column-gap: 2rem;
    flex-wrap: wrap;
    row-gap: 1rem;
    
}

.services__icon{
    display: flex;
    justify-content: start;
    column-gap: 1rem;
    font-size: 2rem;
    margin-bottom: var(--mb-1);
}

.services__content{
    /* position: relative; */
    width: 300px;
    border: 1px solid rgba(0,0,0,0.1);
    background-color: var(--container-color);
    padding: 5rem 0 2rem 2.5rem
}

.tech-icon{
    font-size: 2rem;
    color: var(--title-color)
}


.services__title,.service__modal-title{
    font-size: var(--h3-font-size);
    margin-bottom: var(--mb-1);
    font-weight: var(--font-medium);

}

.services__button{
    color:var(--title-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items: center;
    column-gap: 0.25rem;
    cursor: pointer;
}

.services__button-icon{
    font-size: 1rem;
    transition: 0.3s;
}

.services__button:hover .services__button-icon{
    transform: translateX(00.25rem);
}

.services__modal{
    position: fixed;
    top:0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    z-index: var(--z-modal);
    display: flex;
    justify-content: center;
    align-items: center;
    /* transition: all linear 0.25s;  */
    animation: fadeIn 0.1s;

}

@keyframes fadeIn {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

.services__modal-content{
    width: 600px;
    position: relative;
    background-color: var(--container-color);
    padding: 4.5rem 2.5rem 2.5rem;
    border-radius: 1.5rem;
    animation: growth ease 0.5s;
}

@keyframes growth{
    from{
        transform: scale(0.75);
    }
    to{
        transform: scale(1);
    }
}

.services__modal-close{
    position: absolute;
    top:1.5rem;
    right: 1.5rem;
    font-size: 1.5rem;
    color:var(--title-color);
    cursor: pointer;
}

.services__modal-title,
.services__modal-description{
    text-align: center;
}


.services__modal-description {
    font-size: var(--small-font-size);
    padding: 0 3.5rem;
    margin-bottom: var(--mb-2);

}

.services_check-icon{
    font-size: 1rem;
    margin-right: .75rem;
    color: var(--icon-color);
}

.services__modal-service{
    row-gap: 0.75rem;
}

.services__modal-info{
    font-size: var(--small-font-size);
}


